.section-6 {
    height: 100%;
    width: 100%;
    background-image: url(./../../img/zabor-2.png);
    background-repeat:no-repeat;
    background-position: center;
    background-size: cover;

    .intro-slider{

        &__wrapper{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: row;

            &-left{
                width: 50%;
                height: 100%;
                background: #fff;
                display: flex;
                flex-direction: column;
                
            }
            &-right{
                position: relative;
                width: 50%;
                height: 100%;
                z-index: 5;
                iframe {
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0,0,0,.45) !important;
                    
                }
            }
        }
    }
    &-title{
        color: #555555;
        margin-bottom: 35px !important;
    }
    &-num {

        &-container {
            display: flex;
            flex-direction: column;
        }
        font-size:  var(--fz-xl);
        color: var(--c-primary);
        font-weight: 700;
        display: inline !important;
        transition: color 0.4s ease-in-out;
        &:hover{
            color: var(--c-secondary);
        }
        &-last{
            margin-bottom: 15px !important;
        }
    }
    &-mail{
        margin-bottom: 85px;
        font-size:  var(--fz-m);
        color: #808080;
        font-weight: 700;
    }
    &__content {
        display: flex;
        flex-direction: column;
        row-gap: 40px;
    }
    &__office,
    &__time{
        display: flex;
        flex-direction: column;
        row-gap: 15px;

        span {
            font-size:  var(--fz-xs);
            font-weight: 700;
            text-transform: uppercase;
            color: #cccccc;
        }
        p{
            font-size:  var(--fz-m);
            color: #555555;
        }
    }
    &-container {
        padding: 20px;
        padding-top: 150px;
        padding-left: calc(((100% * 2 - 1500px ) / 2));
        
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        opacity: 0;
        transition: 0.7s ease-in-out;
        transform: translateY(50px);
    }
    &.fadeUppText{
        .section-6{
            &-container {
                opacity: 1;
                transform: translateY(0) !important ;
            }
        }
    }
}

@media (max-height: 900px) {
    .section-6{


        .footer__copyright{
            margin-top: 20px;
        }
        &-mail {
            margin-bottom: 40px;
        }
        &__content {
            row-gap: 25px;
        }
    }
}

@media (max-height: 610px){
    .section-6__content {
        row-gap: 10px;
    }
    .section-6-mail {
        margin-bottom: 10px;
    }
    .section-6-num-last {
        margin-bottom: 10px !important;
    }
    .section-6-title {
        margin-bottom: 10px !important;
    }
    .section-6{
        .footer__copyright{
            padding-top: 15px !important;
            padding-bottom: 15px !important;
        }
    }
}